import React from 'react';
import { Icon, Tooltip } from 'antd';
import { withTranslation } from 'react-i18next';
import { inject, observer } from 'mobx-react';
import { get, observable } from 'mobx';
import dayjs from 'dayjs';
import './style.scss'
import { getFile, storage } from '../../util/util';
import Loading from '../../common/Widget/Loading';
import Nodata from '../../common/Widget/Nodata';
import Checkbox from '../../common/Widget/Checkbox';
import DeleteModal from '../../common/Widget/DeleteModal'
import Chooseframe from '../../common/Widget/Chooseframe';
import event from '../../util/event';
import LazyImage from '../../common/Lazy-image';
import imgload from '../../images/imgload.gif';
import imgload2 from '../../images/imgerror.png';


//ls变量名称
const PLAYLISTSORT = 'PlaylistSort';
const PLAYLISTSORT_1_ISDES = 'PlaylistSort_isdes';


@withTranslation() @inject('playlist', 'rahmen', 'login') @observer
class Playlist extends React.Component {
  constructor(props) {
    super(props);
    this.t = this.props.t;
    this.boxRef = React.createRef();
  }

  @observable menuDisplayId = null;
  @observable rename = false;
  @observable renameid = null;
  @observable ren_defaultValue = '';
  checkAll = false;
  @observable sortList = [
    { id: 1, text: 'updateSort' },
    { id: 2, text: 'creationSort' },
    { id: 3, text: 'photosSort' },
    { id: 4, text: 'nameSort' }
  ];
  @observable defaultSort = {};
  @observable sortindex = 0;
  @observable isdes = false;
  @observable del_visible = false;
  @observable a_deleteId = null;
  @observable set_visible = false;
  @observable setDataList = false;
  @observable ckick_listId = null;
  @observable sortDis = false;
  @observable childW = 0;
  @observable childH = 0;
  @observable columnCount = 0;
  @observable rowCount = 0;

  componentDidMount() {
    let obj = {};
    const idx = storage.get(PLAYLISTSORT);
    const order = storage.get(PLAYLISTSORT_1_ISDES);
    if (!idx && idx !== 0) {
      storage.set(PLAYLISTSORT, 0)
      obj = this.sortList[0];
    } else {
      obj = this.sortList[idx];
      this.sortindex = idx;
    }
    this.defaultSort = obj;
    this.isdes = order || false;
    this.props.playlist.playlist_init(storage.get(PLAYLISTSORT), this.isdes, this.boxRef.current.offsetWidth);

    document.addEventListener('click', this.hide, true)
    document.addEventListener('click', this.sorthide);

    window.addEventListener('resize', this.getWH);

    event.on('addPlaylist', () => {
      this.checkAll = false;
    })

    this.wsinit = event.on('wsinit', (res) => {
      if (res === 1) {
        this.props.playlist.playlist_init(storage.get(PLAYLISTSORT), this.isdes, this.boxRef.current.offsetWidth);
      }
    })
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.hide, true);
    document.removeEventListener('click', this.sorthide);
    window.removeEventListener('resize', this.getWH);
    event.rm(this.wsinit);
    this.props.playlist.replacement();
  }

  getWH = () => {
    this.props.playlist.resize(this.boxRef.current.offsetWidth, this.props.playlist.playList)
  }

  // 更多modal隐藏
  hide = e => {
    if (e.target && e.target.nodeName === 'svg') {
      return;
    }
    this.menuDisplayId = null;
  }
  // 排序modal隐藏
  sorthide = e => {
    if (e.target && e.target.nodeName === 'span') {
      return;
    }
    this.sortDis = false;
  }

  //更多modal显示
  more_show = (e, node) => {
    e.stopPropagation();
    if (this.menuDisplayId) {
      this.menuDisplayId = null;
    } else {
      this.menuDisplayId = node;
      this.sortDis = false;
    }
  }

  //重命名列表start
  renamefn = (e, id, name) => {
    e.stopPropagation();
    this.rename = true;
    this.renameid = id;
    this.ren_defaultValue = name;
  }
  //重命名列表确定
  ren_handleconfirm = (name) => {
    const { renameid } = this;
    this.props.playlist.playlist_rename(renameid, name);
    this.rename = false;
    this.renameid = null;
  }
  ren_handlecancel = () => {
    this.rename = false;
    this.renameid = null;
  }
  //重命名列表end


  //删除列表start
  delete(e, id) {
    e.stopPropagation();
    this.del_visible = true;
    this.a_deleteId = [id];
  }
  del_handleconfirm = () => {
    const { a_deleteId } = this;
    this.del_visible = false;
    this.checkAll = false;
    this.a_deleteId = null;
    this.props.playlist.playlist_delete(a_deleteId)
  }
  del_handlecancel = () => {
    this.del_visible = false;
    this.a_deleteId = null;
  }
  //删除列表end

  //排序start
  sortEldis(e) {
    e.nativeEvent.stopImmediatePropagation();
    this.menuDisplayId = null;
    this.sortDis = !this.sortDis;
  }
  changeIndex(i) {
    storage.set(PLAYLISTSORT, i);
    if (this.sortindex === i) {
      this.isdes = !this.isdes;
      storage.set(PLAYLISTSORT_1_ISDES, this.isdes);
    } else {
      this.sortindex = i;
      storage.set(PLAYLISTSORT_1_ISDES, this.isdes);
    }
    this.defaultSort = this.sortList[i];
    this.props.playlist.playlist_sort1(i, this.isdes);
  }
  //排序end

  //全勾选/选择start
  checkAllfn = () => {
    this.checkAll = !this.checkAll;
    this.props.playlist.checkAllplaylist(this.checkAll);
  }
  //勾选/选择end

  //绑定的相框
  chooseframe(e, id) {
    e.stopPropagation();
    this.set_visible = true;
    this.setDataList = true;
    this.ckick_listId = id;
    this.props.playlist.playlist_detail_frames(id, () => {
      this.setDataList = false;
    });
    event.on('framesNum', () => {
      this.props.playlist.playlist_detail_frames(id, () => {
        this.props.playlist.playlist_init();
      });
    })
  }

  //相框操作隐藏
  framesHide = () => {
    event.rmName('framesNum')
    this.set_visible = false;
  }

  //同步相框播放确定
  frameonPlay = (node) => {
    this.props.playlist.frameonPlay(this.ckick_listId, node)
  }

  //跳转列表详情
  goPlaylistdetails(id, name, created) {
    this.props.playlist.goUrl(name, created);
    this.props.history.push(`/playlist/playlistdetails/${id}`)
  }

  render() {
    const { t } = this.props;
    const { OS } = this.props.login;
    const {
      rename,
      ren_defaultValue,
      sortList,
      defaultSort,
      del_visible,
      set_visible,
      isdes,
      sortDis,
      setDataList,
      menuDisplayId,
      ckick_listId,
      sortindex } = this;
    const {
      playList,
      pitchOn,
      removeplayList,
      frameList,
      createPlaylist,
      frame_list,
      firstInit,
      invert } = this.props.playlist;
    let playlistEl;
    const time = sortindex === 1 ? `${t('CreationTime')}：` : `${t('LastUpdate')}：`;
    if (playList.length > 0) {
      playlistEl = playList.map((item) => {
        return (
          <div key={item.id} className='list' style={{ width: item.w, height: item.h }}>
            <div
              className={`playlist-list ${get(item, 'check') ? 'playlist-list-check' : ''}`}
              onClick={() => { this.goPlaylistdetails(item.id, item.name, item.created) }}>
              <span
                style={OS === 'tab' ? { visibility: 'inherit' } : {}}
                className={`icon-check-s treeNodeUnselectable`}
                onClick={(e) => { pitchOn(e, item.id, item.frame_default, () => { this.checkAll = true }, () => { this.checkAll = false }) }}>
                <i className='iconfont' style={get(item, 'check') ? {} : { visibility: "hidden" }}>&#xe62d;</i>
              </span>
              <div className='top-img'>
                <div className="img1"
                  style={item.profile ? { backgroundImage: `url(${imgload})` } : { backgroundImage: `url(${imgload2})`, backgroundSize: '100% 130%' }}
                >
                  {item.profile && <LazyImage
                    src={getFile.file_s(item.profile.id)}
                    alt={item.profile.id}
                    w='226px'
                  />}
                </div>
                <div className="img2-4">
                  <span className="img2" style={item.profile_1 ? { backgroundImage: `url(${imgload})` } : { backgroundImage: `url(${imgload2})`, backgroundSize: '100% 100%' }}>
                    {item.profile_1 && <LazyImage
                      src={getFile.file_s(item.profile_1.id)}
                      alt={item.profile_1.id}
                      w='74.5px'
                    />}
                  </span>
                  <span className="img3" style={item.profile_2 ? { backgroundImage: `url(${imgload})` } : { backgroundImage: `url(${imgload2})`, backgroundSize: '100% 100%' }}>
                    {item.profile_2 && <LazyImage
                      src={getFile.file_s(item.profile_2.id)}
                      alt={item.profile_2.id}
                      w='74.5px'
                    />}
                  </span>
                  <span className="img4" style={item.profile_3 ? { backgroundImage: `url(${imgload})` } : { backgroundImage: `url(${imgload2})`, backgroundSize: '100% 100%' }}>
                    {item.profile_3 && <LazyImage
                      src={getFile.file_s(item.profile_3.id)}
                      alt={item.profile_3.id}
                      w='74.5px'
                    />}
                  </span>
                </div>
              </div>
              <div className='playlist-information'>
                <div className='p-i-top'>
                  <Tooltip placement="bottom" overlayClassName='tooltip' title={get(item, 'name')}>
                    <p className='playlist-name'>{get(item, 'name')}</p>
                  </Tooltip>
                  <p className='update-time'>
                    <span>{time}{sortindex === 1 ? dayjs(item.created).format(t('L')) : dayjs(item.updated).format(t('L'))}</span>
                  </p>
                  {item.frame_default > 0 && < span className='frames_def'>{t('default')}</span>}
                </div>
                <div className='p-i-bottom'>
                  <div className='photo-num'>
                    <p className='num1'>{item.filecount ? item.filecount : 0}</p>
                    <p className='text1'>{t('Photo')}</p>
                  </div>
                  <div className='frame-num'>
                    <p className='num1'>{item.frame_count}</p>
                    <p className='text1'>{t('photoframe')}</p>
                  </div>
                  <div className='more treeNodeUnselectable'>
                    <span className='moreIcon' onClick={(e) => { this.more_show(e, item.id) }}><Icon type="ellipsis" /></span>
                    <div className='rename' style={menuDisplayId === item.id ? { display: 'block' } : {}}>
                      <ul>
                        <li onClick={(e) => { this.renamefn(e, item.id, item.name) }}>
                          <span><Icon type="edit" theme="filled" />{t('rename')}</span>
                        </li>
                        {item.frame_default === 0 &&
                          <li onClick={(e) => { this.delete(e, item.id) }}>
                            <span><Icon type="delete" theme="filled" />{t('delete')}</span>
                          </li>
                        }
                        {/* <li onClick={(e) => { this.chooseframe(e, item.id) }}>
                        <span><Icon type="picture" />{t('photoframe')}</span>
                      </li> */}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div >
          </div>
        )
      })
    } else {
      playlistEl = <Nodata />
    }
    let num = 0;
    removeplayList.forEach(item => {
      if (!item.true) {
        num += 1;
      }
    });

    return (
      <div className='playlist'>
        <div className='new-playlist'>
          <div className='new-playlist-btn'>
            <button onClick={createPlaylist}>{t('NewPlaylist')}</button>
          </div>
          <div className='playlist-num'>
            <div className='playlist-num-icon'><i className='iconfont'>&#xe605;</i></div>
            <div className='num-text'>
              <p className='text'>{t('playlist')}</p>
              <p className='num'>{playList.length > 0 ? playList.length : 0}</p>
            </div>
          </div>
        </div>
        <div className='playlist-operation'>
          <div className='checkAll'>
            <i
              className={`iconfont check-frame treeNodeUnselectable ${playList.length === removeplayList.length && playList.length > 0 ? 'icon-xuanzhong1' : 'icon-weixuanzhong'}`}
              onClick={this.checkAllfn}></i>

            <span className='checkAll-text'>{num > 0 ? `${t('Selected')} ${num} ${t('list')}` : t('Allelection')}</span>
            {removeplayList.length > 0 && <span onClick={invert} className='invert-Selection'>{t('Reverseselection')}</span>}
          </div>
          <div className='playlist-sort'>
            <span className='sort treeNodeUnselectable' onClick={(e) => { this.sortEldis(e) }}>{t(defaultSort.text)}{isdes ? <Icon type="arrow-up" /> : <Icon type="arrow-down" />}</span>
            <div className='sort-list' style={sortDis ? { display: 'block' } : { display: 'none' }}>
              <ul>
                {
                  sortList.map((item, index) => {
                    return (
                      <li key={item.id} onClick={() => { this.changeIndex(index) }}>
                        <span>
                          <Icon type="check" className={item.id === defaultSort.id ? 'check-icon' : null} />
                          {t(item.text)}</span>
                      </li>
                    )
                  })
                }
              </ul>
            </div>
          </div>
          <div className='playlist-delete' style={removeplayList.length > 0 ? null : { visibility: 'hidden' }}>
            <span onClick={() => { this.del_visible = true; }}><Icon type="delete" />{t('delete')}</span>
          </div>
        </div>
        <div className='playlist-main'>
          <div className='playlist-main-box' ref={this.boxRef}>
            {firstInit ? playlistEl : <Loading />}
          </div>
        </div>

        {
          set_visible && <Chooseframe
            setDataList={setDataList}
            frameList={frameList}
            frame_list={frame_list}
            ckick_listId={ckick_listId}
            framePlay={this.frameonPlay}
            handlecancel={this.framesHide} />
        }

        {
          rename ? <Checkbox
            title={t('rename')}
            defaultValue={ren_defaultValue}
            handlecancel={this.ren_handlecancel}
            handleconfirm={this.ren_handleconfirm} /> : null
        }

        {
          del_visible ? <DeleteModal
            title={t('Deleteconfirm')}
            text={t('list')}
            custom={t('Deletedefault')}
            handlecancel={this.del_handlecancel}
            handleconfirm={this.del_handleconfirm} /> : null
        }
      </div >
    );
  }
}

export default Playlist;
