import React, { Component } from 'react';
import { Icon, Tooltip, message } from 'antd';
import { inject, observer } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import './style.scss';
import Loading from './Loading';
import { get, observable, set, remove } from 'mobx';
import { strRep, getFile, myfilter } from '../../util/util';

@withTranslation() @inject('playlist', 'rahmen', 'login') @observer
class SetModal extends Component {
  constructor(props) {
    super(props);
    this.filtrRef = React.createRef();
  }

  @observable visible = false;
  @observable frame_list = [];
  @observable asyncframelist = [];
  @observable framelistselect = []; //要添加的相框

  handleOK = () => {
    if (this.framelistselect.length !== this.asyncframelist.length) {
      this.props.playlist.playlist_save_frames(this.props.ckick_listId, this.asyncframelist);
    }
    this.visible = false;
  }

  show = () => {
    const arr = this.props.rahmen.rahmenList.slice(0);
    const { frameList } = this.props;
    this.asyncframelist = [];
    arr.forEach(item => {
      remove(item, 'check2');
      frameList.forEach(el => {
        if (el.node === item.node) {
          set(item, { check2: true })
          this.asyncframelist.push(item.node)
          this.framelistselect.push(item.node)
        }
      });
    });
    this.visible = true;
    this.frame_list = arr;
  }

  SetpitchOn = (frame) => {
    if (frame.playlist_id === this.props.ckick_listId) {
      message.warning('列表为相框的默认列表，不允许取消');
      return;
    }
    const { frame_list, asyncframelist } = this;
    let idx = frame_list.findIndex(item => frame.node === item.node);
    if (frame_list[idx].check2) {
      remove(frame_list[idx], 'check2');
      asyncframelist.remove(frame_list[idx].node);
    } else {
      set(frame_list[idx], { 'check2': true })
      asyncframelist.push(frame_list[idx].node)
    }
  }

  hide = () => {
    this.visible = false;
  }

  //筛选
  filtration = () => {
    const val = this.filtrRef.current.value;
    const oldlist = this.props.rahmen.rahmenList.slice(0);
    const arr = myfilter(oldlist, 'name', val);
    if (!val) {
      this.frame_list = oldlist;
    } else {
      this.frame_list = arr;
    }
  }

  render() {
    const { visible, frame_list } = this;
    const { OS } = this.props.login;
    const { t, handlecancel, setDataList, frameList, ckick_listId, framePlay } = this.props;
    let frameEl;
    if (setDataList) {
      frameEl = (
        <div className='rahmenlist-box'>
          <Loading />
        </div>
      )
    } else {
      if (frameList.length > 0) {
        frameEl = (
          <div className='rahmenlist-box'>
            <ul>
              {
                frameList.map((item) => {
                  return (
                    <li className='clearfix' key={item.node}>
                      <div className='rahmenlist-box-left'>
                        <p className='frame-name'>{item.name}</p>
                        <p className='frame-serial'>SN : {strRep(item.serial)}</p>
                      </div>
                      <div className='rahmenlist-box-btn'>
                        <span className='isdefault' style={item.playlist_id === ckick_listId ? null : { visibility: 'hidden' }}>默认</span>
                        <Tooltip title="点击播放到相框" overlayClassName='tooltip'>
                          <span ><Icon onClick={() => { framePlay([item.node]) }} className='play-btn' type="play-square" theme="filled" /></span>
                        </Tooltip>
                      </div>
                    </li>
                  )
                })
              }
            </ul>
          </div>
        )
      } else {
        frameEl = (
          <div className='rahmenlist-box'>
            <span className='no-frame'>没有绑定的相框</span>
          </div>
        )
      }
    }


    return (
      <div id='set-modal'>
        <div className='setModal-warp'>
          <div className='header'>
            <h3>相框</h3>
            <span onClick={handlecancel}><Icon type="close" /></span>
          </div>
          <div className='setModal-warp-boby'>
            {frameEl}
          </div>
          <div className='foot-btn'>
            <span className="select-frame-btn" onClick={this.show}>选择相框</span>
          </div>

          {
            visible &&
            <div className='select-frame-module'>
              <div className='select-frame-body'>
                <span
                  className='select-frame-module-close'
                  onClick={this.hide}>
                  <Icon type="close" />
                </span>
                <div className='action-bar clearfix'>
                  <div className='Selectlist-filtrate'>
                    <input ref={this.filtrRef} type="text" placeholder={t('Screen')} onInput={this.filtration} />
                    <Icon type="filter" />
                  </div>
                </div>
                <div className='frame_list'>
                  {
                    frame_list.length > 0 ? frame_list.map(item => {
                      return (
                        <div
                          key={item.node}
                          style={OS === 'tab' ? { borderColor: '#FFF' } : {}}
                          className={`choose-framer-list ${get(item, 'check2') ? 'choose-framer-list-check' : null}`}
                          onClick={() => { this.SetpitchOn(item) }}>
                          <span
                            style={OS === 'tab' ? { visibility: 'inherit' } : {}}
                            className={`choose-framerlist-check`}>
                            <Icon type="check" />
                          </span>
                          <div className='choose-framer-cover'>
                            <div className='cover-img'>
                              <span
                                className='back-img'
                                style={item.profile ? { background: `url("${getFile.file_s(item.profile.id)}") no-repeat`, backgroundSize: '100% 100%' } : {}}></span>
                            </div>
                          </div>
                          <div className='choose-framer-name-series'>
                            <div className='name clearfix'>
                              <p className='frame-name ellipsis'>{item.name}</p>
                              <div className='admin_default'>
                                <span
                                  className='admin'
                                  style={item.is_admin ? { visibility: 'initial' } : { visibility: 'hidden' }}>
                                  {t('Admin')}
                                </span>
                                <span
                                  className='default'
                                  style={item.playlist_id === ckick_listId ? { visibility: 'initial' } : { visibility: 'hidden' }}>
                                  默认
                              </span>
                              </div>
                            </div>
                            <p className='series'>SN : {strRep(item.serial)} </p>
                          </div>
                        </div>
                      )
                    }) : <span className='nodata'>没有找到数据</span>
                  }
                </div>
                <div className='select-frame-b-btn'>
                  <button onClick={this.handleOK}>确定</button>
                </div>
              </div>
            </div>
          }
        </div>
      </div >
    );
  }
};

export default SetModal;